import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import SetImg from '@/components/SetImg'
import test from '@/components/test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/setImg',
      component: SetImg
    },
    {
      path: '/test',
      component: test
    }
  ]
})



