<template>
  <div class="main">
    <p>点击时间选择：<span class="current" @click="isTimeShow = true">{{currentDate | format}}</span></p>
    <transition name="fade" mode="out-in" appear>
      <van-datetime-picker
        v-show="isTimeShow"
        v-model="currentDate"
        type="datetime"
        @confirm="isTimeShow = false"
        @cancel="isTimeShow = false"/>
      </transition>
    <van-uploader :after-read="onRead" accept="image/gif, image/jpeg">
      <div class="upload">
        <van-icon name="photograph" class="current current-upload"/>
      </div>
    </van-uploader>
    <div class="imgList">
      <img v-show="showImg" ref="img" :src="imgUrl" alt="">
    </div>
    <van-button :disabled="!showImg" size="large" type="primary" @click="submit">提交</van-button>
  </div>
</template>

<script>
import moment from 'moment'
import { ImagePreview } from 'vant'
// import EXIF from 'exif-js'

export default {
  filters: {
    format (data) {
      return moment(data).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  data() {
    const {imgUrl, time, date} = this.$route.query
    return {
      isTimeShow: false,
      currentDate: new Date(),
      imgUrl: '',
      showImg: false,
      isRotate: false
    }
  },
  methods: {
    changeExif (file) {
      let orientation = ''
      EXIF.getData(file, () => {
        orientation = EXIF.getTag(file, 'Orientation')
      })
      return file
    },
    onRead (file) {
      this.showImg = false
      this.imgUrl = file.content
      const img = this.$refs.img
      img.onload = () => {
        if (img.width > img.height) {
          this.isRotate = true
        } else {
          this.isRotate = false
        }
        this.showImg = true
      }
    },
    submit () {
      this.$router.push({
        path: '/setImg',
        query: {
          date: moment(this.currentDate).format('YYYYMMDD'),
          time: moment(this.currentDate).format('HHmmss'),
          imgUrl: this.imgUrl,
          isRotate: this.isRotate
        }
      })
    }
  }
}
</script>

<style scoped>
.imgList{
  margin: 20px 0;
}
.current{
  color: #03a9f4;
  font-weight: bolder;
  font-size: 20px;
}
.upload{
  width: 100px;
  height: 100px;
  border: 2px solid #03a9f4;
  border-radius: 50%;
  text-align: center;
  line-height: 108px;
}
.current-upload{
  font-size: 30px;
}
img{
  width: 100%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.main{
  padding: 20px;
}
</style>



