<template>
  <div>
		<img v-show="false" ref="img" :src="imgUrl" alt="" style="width: 100%;">
    <canvas ref="canvas" id="canvas" style="width: 100%;">
        你的浏览器不支持canvas，建议使用最新版的Chrome浏览器。
    </canvas>
    <van-button type="primary" @click="save">保存图片</van-button>
  </div>
</template>

<script>
import imgRow from '@/assets/image/row.png'
import text from '@/assets/image/text.png'
import a from '@/assets/image/a.jpg'
import b from '@/assets/image/b.jpg'
import c from '@/assets/image/c.jpg'
import d from '@/assets/image/d.jpg'
import e from '@/assets/image/e.jpg'
import f from '@/assets/image/f.jpg'
import g from '@/assets/image/g.jpg'
import h from '@/assets/image/h.jpg'
import i from '@/assets/image/i.jpg'
import j from '@/assets/image/j.jpg'
import t_a from '@/assets/image/t_a.jpg'
import t_b from '@/assets/image/t_b.jpg'
import t_c from '@/assets/image/t_c.jpg'
import t_d from '@/assets/image/t_d.jpg'
import t_e from '@/assets/image/t_e.jpg'
import t_f from '@/assets/image/t_f.jpg'
import t_g from '@/assets/image/t_g.jpg'
import t_h from '@/assets/image/t_h.jpg'
import t_i from '@/assets/image/t_i.jpg'
import t_j from '@/assets/image/t_j.jpg'
export default {
  data () {
    const {imgUrl, time, date, isRotate} = this.$route.query
		const datePosition = [
			[263, 900],
			[263 + (1 * 25), 900],
			[263 + (2 * 25), 900],
			[263 + (3 * 25), 900],
			[263 + (5 * 25), 900],
			[263 + (6 * 25), 900],
			[263 + (8 * 25), 900],
			[263 + (9 * 25), 900],
		]
		const timePosition = [
			[100, 955],
			[100 + (1 * 75), 955],
			[100 + (3 * 65), 955],
			[100 + (4 * 68), 955],
			[100 + (6 * 65), 955],
			[100 + (7 * 66), 955]
		]
		const setImgList = (str, position) => {
			return str ? str.split('').map((item, i) => {
				return {
					item,
					position: position[i]
				}
			}) : []
		}
		const getRandom = function (min, max) {
			return parseInt(Math.random() * (max - min + 1))
		}
		const timeList = setImgList(time, timePosition).map((item, i) => {
			if (i === 4) {
				item.item = getRandom(0, 5)
			}
			if (i === 5) {
				item.item = getRandom(0, 9)
			}
			return item
		})
    return {
			isRotate, // 图片是否要旋转
      imgUrl,
      dateList: setImgList(date, datePosition),
			timeList :timeList,
			imgDateList: [a, b, c, d, e, f, g, h, i, j],
			imgTimeList: [t_a, t_b, t_c, t_d, t_e, t_f, t_g, t_h, t_i, t_j],
			currentUrl: ''
    }
  },
  mounted () {
		this.initDraw()	
  },
	methods: {
		initDraw () {
			const _this = this
			class Draw {
				constructor (el) {
					this.el = el
					this.canvas = document.getElementById(this.el)
					this.ctx = this.canvas.getContext('2d')
					this.currentWidth = 960
					this.currentHeight = 1280
				}
				init (img, dates, times) {
					this.img = img
					this.dates = dates
					this.times = times
					this.imgWidth = img.width
					this.imgHeight = img.height
					this.segmentImg()
				}
				segmentImg () {
					const that = this
					this.canvas.width = this.currentWidth
					this.canvas.height = this.currentHeight
					if (_this.isRotate) {
						// 旋转图片
						const width = this.currentWidth/ 2
						const height = this.currentHeight / 2
						this.ctx.save()
						this.ctx.translate(width, height)
						this.ctx.rotate(Math.PI / 2)
						this.drawImg(this.img, {x: -height, y: -width, w: this.currentHeight, h: this.currentWidth}, () => {
							that.ctx.restore()
							that.setImgRow()
						})
					} else {
						this.drawImg(this.img, {x: 0, y: 0, w: this.currentWidth, h: this.currentHeight}, () => {
							that.setImgRow()
						})
					}
				}
				setImgRow () {
					const img = new Image()
					img.src = imgRow
					const x = 70
					const y = 880
					img.onload = () => {	
						this.ctx.drawImage(img, x, y)
						this.ctx.save()
						this.drawText()
					}
				}
				drawText () {
					const img = new Image()
					img.src = text
					const x = 70
					const y= 1100
					img.onload = () => {	
						this.ctx.drawImage(img, x, y)
						this.ctx.save()
						this.setImgList(this.dates)
						this.setImgList(this.times)
					}
				}
				setImgList ({imgList, list}) {
					list.map((data, index) => {
						const { item, position } = data
						const img = new Image()
						img.src = imgList[item]
						img.onload = () => {
							this.ctx.drawImage(img, position[0], position[1])
						}
					})
				}
				drawImg (img, {x, y, w, h}, cb) {
					img.onload = () => {
						if (w && h) {
							this.ctx.drawImage(img, x, y, w, h)
						} else {
							this.ctx.drawImage(img, x, y)
						}
						cb ? cb() : null
					}
				}
			}
			const draw = new Draw('canvas')
			draw.init(this.$refs.img, {
				imgList: this.imgDateList,
				list: this.dateList
			}, {
				imgList: this.imgTimeList,
				list: this.timeList
			})
			return draw
		},
		save () {
			window.location.href = this.$refs.canvas.toDataURL()
		}
	}
}
</script>



